import React from 'react';
import { shape, object, arrayOf, bool, func, string } from 'prop-types';
import Link from 'gatsby-link';
import cx from 'classnames';

import { Icon, Divider } from '../../ui';

import './MobileNav.scss';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('HEADER') > -1
	});

export default class MobileNav extends React.Component {
	componentDidMount() {
		document.body.style.overflow = 'hidden';
	}

	componentWillUnmount() {
		document.body.style.overflow = 'visible';
	}

	render() {
		const { data } = this.props;

		const list = [];

		getNavItems(data.allMarkdownRemark.edges).forEach((item, index) => {
			list.push(
				<Link
					to={item.node.fields.slug}
					className="main-navigation"
					key={index} >
					{item.node.frontmatter.title}
				</Link>
			);

			list.push(<Divider key={index + 'd'} />);
		});

		return (
			<div className="mobileNav__container">
				<Divider />
				{list}
			</div>
		);
	}
};

MobileNav.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	}),
	isTop: bool,
	withNav: bool,
	path: string
}



